<template>
  <div class="answer-warp">
    <Header>
      <a class="publish-btn" href="javascript:;" slot="right" @click="toPublish">提交</a>
    </Header>
    <div class="answer-title">
      <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/question.png" alt="">
      <h2>{{getQuestionInfo.question}}</h2>
    </div>
    <div class="answer-edit">
      <textarea v-model="inputText" placeholder="我知道，我来回答~" maxlength="140"></textarea>
      <span><strong>{{inputText.length}}</strong>/140</span>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import { releaseProductAnswer } from '@/http/service/goods'
import { mapGetters } from 'vuex'
export default {
  name: 'goodsAnswerAdd',
  components: {
    Header,
  },
  data() {
    return {
      inputText: ''
    }
  },
  computed: {
    ...mapGetters('answerArea', ['getQuestionInfo']),
    ...mapGetters(['mId']),
  },
  methods: {
    toPublish() {
      if (!this.inputText) {
        this.$toast('请输入内容');
        return;
      }
      if (this.inputText > 140) {
        this.$toast('内容超出140哥字符');
        return;
      }
      releaseProductAnswer({
        questionId: this.getQuestionInfo.questionId,
        answerUserId: this.mId,
        answer: this.inputText,
        sku: this.getQuestionInfo.sku
      }).then(res => {
        let { success, message } = res;
        if (success) {
          this.$toast('发布成功')
          this.$router.back()
        } else {
          this.$toast(message);
        }
      })
    }
  }
}
</script>

